.Header {
  border-bottom: 2px solid lightgray;
  box-sizing: border-box;
  background-color: #e8ebf4;
  font-weight: 500;
  color: grey;
  padding: 1.2%;
  font-family: "Roboto", sans-serif;
}
.Header,
.Header .menu,
.Header .name {
  display: flex;
}

.Header .name,
.Header .menu {
  width: 50%;
  box-sizing: border-box;
}

.Header .menu {
  justify-content: flex-end;
  min-width: 360px;
  flex-wrap: nowrap;
}

.Header .name {
  justify-content: flex-start;
  min-width: 210px;
}
.Header .name div {
  margin: 5px;
}

.Header .menu-item {
  margin: 5px;
  margin-right: 10px;
  color: black;
  cursor: pointer;
}

.Header .navlink:hover {
  color: #293d77;
}

.Header .navlink {
  color: grey;
  text-decoration: none;
}

.Header .active-navlink {
  color: #293d77;
}
