.Car{
  width:300px;
  height:150px;
  position:fixed;
  bottom:0;
  left:100px;
}

.Car .hull{
  width:500px;
  position:fixed;
  bottom:0px;
  left:1%;
  animation: moveCar 100s linear 1;
  animation-fill-mode: forwards;
}

#wheel1, #wheel2{
  position:fixed;
  bottom:0px;
  left:calc(1% + 15px);
  width:48px;
  animation: moveWheel1 100s linear 1, spinWheel 2.5s linear infinite;
  animation-fill-mode: forwards;
}

#wheel2{
  left:calc(1% + 151px);
  animation: moveWheel2 100s linear 1, spinWheel 2.5s linear infinite;
  animation-fill-mode: forwards;
}

@keyframes moveCar {
  from{
    left:1%;
  }
  to{
    left:101%;
  }
}

@keyframes moveWheel1{
  from{
    left:calc(1% + 15px);
  }
  to{
    left:calc(101% + 15px);
  }
}

@keyframes moveWheel2{
  from{
    left:calc(1% + 151px);
  }
  to{
    left:calc(101% + 151px);
  }
}

@keyframes spinWheel{
  from{
    transform: rotate(0deg);
  }
  to{
    transform:rotate(360deg);
  }
}
